/*-----------------------------------------26. Pricing-------------------------------------------*/


/*--------- Style 1 ----------*/

.pricing-box {
    position: relative;
    border-radius: 5px;
    background: #fff;
    border: 1px solid #eee;
    padding: 50px 50px;
    box-shadow: 0 20px 30px rgba(0, 0, 0, .03);
    margin-bottom: 15px;
    -webkit-transition-duration: .3s;
    transition-duration: .3s;
}

.pricing-box:hover {
    transform: translateY(-10px);
    box-shadow: 0 20px 30px rgba(0, 0, 0, .09);
}

.pricing-box-icon {
    text-align: center;
}

.pricing-box-icon i {
    color: #777;
    font-size: 50px;
}

.pricing-box h4 {
    text-align: center;
    color: #222;
    font-size: 20px;
    font-weight: 500;
    margin-top: 15px;
}

.pricing-box h2 {
    text-align: center;
    color: #ef4736;
    font-size: 35px;
    font-weight: 400;
    margin-top: 10px;
}

.pricing-box h2 sup {
    color: #ef4736;
    font-size: 25px;
    font-weight: 400;
}

.pricing-box h2 span {
    color: #999;
    font-size: 13px;
    font-weight: 500;
}

.pricing-box ul {
    margin-top: 35px;
}

.pricing-box ul li {
    font-size: 14px;
    color: #777;
    font-weight: 500;
    line-height: 1.5;
    padding: 6px 0px;
}

.pricing-box ul li i {
    font-size: 14px;
    color: #ef4736;
    float: right;
    margin-top: 10px;
}

.pricing-box-button {
    text-align: center;
}

.pricing-box-button a {
    display: inline-block;
    position: relative;
    font-size: 13px;
    color: #ef4736;
    font-weight: 500;
    border: 2px solid #ef4736;
    background: transparent;
    border-radius: 5px;
    padding: 10px 30px;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    margin-top: 35px;
}

.pricing-box-button a:hover {
    color: #fff;
    background: #ef4736;
}

.pricing-box-button-active a {
    color: #fff;
    background: #ef4736;
}

.pricing-box-button-active a:hover {
    color: #ef4736;
    background: #fff;
}


/*--------- Style 2 ----------*/

.pricing-section {
    position: relative;
    transform: scale(1.05);
    text-align: center;
    background: #fff;
    border: 2px solid #ef4736;
    border-radius: 5px;
    padding-bottom: 35px;
    margin-top: 20px;
    z-index: 2;
    -webkit-transition-duration: .3s;
    transition-duration: .3s;
}

@media (max-width: 767px) {
    .pricing-section {
        transform: scale(1);
    }
}

.pricing-section-advanced {
    transform: scale(1);
    background: rgba(243, 80, 7, 0.05);
    border: 2px solid transparent;
    z-index: 1;
    -webkit-transition-duration: .3s;
    transition-duration: .3s;
}

.pricing-section-price {
    background: transparent;
    padding: 25px 0px;
}

.pricing-section-price h2 {
    font-family: 'Montserrat', sans-serif;
    color: #ef4736;
    font-size: 60px;
    font-weight: 700;
}

.pricing-section-price h2 sup {
    color: #ef4736;
    font-size: 35px;
    font-weight: 700;
    margin-right: 5px;
}

.pricing-section-price h4 {
    color: #222;
    font-size: 19px;
    font-weight: 600;
    line-height: 1.7;
}

.pricing-section-price h6 {
    color: #999;
    font-size: 15px;
    font-weight: 500;
}

.pricing-section ul {
    padding: 0px 40px;
    margin-top: 5px;
}

.pricing-section ul li {
    color: #777;
    text-align: left;
    font-size: 15px;
    border-bottom: 1px solid #eee;
    padding: 10px 0px;
}


/*--------- Style 3 ----------*/

.pricing-box-3 {
    position: relative;
    background: #fff;
    box-shadow: 0 20px 30px rgba(0, 0, 0, .05);
    padding: 30px 30px 40px 30px;
    -webkit-transition-duration: .3s;
    transition-duration: .3s;
}

.pricing-box-3:hover {
    box-shadow: 0 20px 30px rgba(0, 0, 0, .1);
}

.pricing-box-3-category {
    padding: 20px 0px;
    border-bottom: 1px solid #eee;
}

.pricing-box-3-category h4 {
    color: #ef4736;
    font-size: 20px;
    font-weight: 500;
}

.pricing-box-3-category h5 {
    color: #999;
    font-size: 14px;
    font-weight: 500;
    margin-top: 3px;
}

.pricing-box-3 h2 {
    font-family: 'Montserrat', sans-serif;
    font-size: 45px;
    color: #222;
    font-weight: 500;
    margin-top: 20px;
}

.pricing-box-3 h2 span {
    font-size: 14px;
    color: #999;
    font-weight: 500;
    margin-left: 5px;
    -webkit-transition-duration: .3s;
    transition-duration: .3s;
}

.pricing-box-3:hover h2 span {
    color: #ef4736;
}

.pricing-box-3 ul {
    margin-top: 20px;
}

.pricing-box-3 ul li {
    color: #999;
    font-size: 14px;
    line-height: 1.5;
    padding: 5px 0px;
}

.pricing-box-3 ul li i {
    margin-right: 5px;
}

.ti-check {
    color: #4a8fff;
    font-size: 12px;
}

.ti-close {
    color: #F00;
    font-size: 12px;
}

.pricing-box-3-button {
    display: inline-block;
    position: relative;
    font-size: 15px;
    color: #ef4736;
    font-weight: 500;
    border: 1.5px solid #ef4736;
    background: #fff;
    border-radius: 5px;
    padding: 11px 20px;
    -webkit-transition-duration: .3s;
    transition-duration: .3s;
}

.pricing-box-3:hover .pricing-box-3-button {
    color: #222;
    border: 1.5px solid #222;
}

.pricing-box-3-button:hover {
    box-shadow: 0 10px 10px rgba(0, 0, 0, .09);
