/*--------- Style 1 ----------*/

.shop-1 {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: auto;
    text-align: center;
    cursor: pointer;
    margin: 15px 0px;
    -webkit-transition-duration: 0.4s;
    transition-duration: 0.4s;
}

.shop-1:hover {
    -webkit-box-shadow: 0px 3px 22px 0px rgba(50, 50, 50, 0.2);
    -moz-box-shadow: 0px 3px 22px 0px rgba(50, 50, 50, 0.2);
    box-shadow: 0px 3px 22px 0px rgba(50, 50, 50, 0.2);
}

.shop-1 img {
    position: relative;
    display: block;
    width: 100%;
    height: auto;
}

.shop-1 {
    background: transparent;
    -webkit-transition-duration: 0.4s;
    transition-duration: 0.4s;
}

.shop-1:hover {
    background: #000;
}

.shop-1 h6 {
    position: absolute;
    bottom: 10px;
    right: 0;
    left: 0;
    font-size: 18px;
    color: #fff;
    padding: 30px 5px;
}

.shop-1 h6:hover {
    color: #fff;
}

.shop-1 p {
    position: absolute;
    bottom: -15px;
    right: 0;
    left: 0;
    font-size: 15px;
    color: #fff;
    padding: 25px 30px;
}

.shop-1 p span {
    text-decoration: line-through;
    color: #aaa;
    padding: 0px 5px;
}

.shop-1 h6 {
    text-transform: none;
    opacity: 0;
    -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
    transition: opacity 0.35s, transform 0.35s;
    -webkit-transform: translate3d(0, 50px, 0);
    transform: translate3d(0, 50px, 0);
}

.shop-1 p {
    text-transform: none;
    opacity: 0;
    -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
    transition: opacity 0.35s, transform 0.35s;
    -webkit-transform: translate3d(0, 50px, 0);
    transform: translate3d(0, 50px, 0);
}

.shop-1:hover img {
    -webkit-transform: translate3d(0, -80px, 0);
    transform: translate3d(0, -80px, 0);
}

.shop-1:hover h6 {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.shop-1:hover p {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}


