/*-----------------------------------------44. Revolution Slider-------------------------------------------*/

.rev_slider_wrapper {
    overflow: hidden;
}

.bg-arrows .tparrows {
    background: rgba(0, 0, 0, .2);
    border-radius: 50px;
    width: 60px;
    height: 60px;
    padding-top: 10px;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
}

.bg-arrows .tparrows:before {
    font-size: 17px
}

.tparrows {
    background: rgba(0, 0, 0, .0);
    border-radius: 50px;
    width: 60px;
    height: 60px;
    padding-top: 10px;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
}

.tparrows:hover {
