/*-----------------------------------------
	1. General
-------------------------------------------*/
.container {
  max-width: 1200px;
}

html,
body {
  font-family: Poppins;
  color: #1c303e;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Poppins;
  color: #222;
  line-height: 1.3;
  margin: 0px;
}

h1,
h2,
h3,
h4 {
  font-weight: 700;
}

h5,
h6 {
  font-weight: 600;
}

img {
  max-width: 100%;
  height: auto;
  -webkit-transition-duration: .3s;
  transition-duration: .3s;
}

a {
  color: #222;
  -webkit-transition-duration: .3s;
  transition-duration: .3s;
}

a:active,
a:hover,
a:focus {
  outline: 0 none;
  text-decoration: none;
}

button {
  cursor: pointer;
  background: none;
  outline: 0 none !important;
  border: none;
}

iframe {
  border: 0px;
}

input,
textarea {
  outline: none;
  max-width: 100%;
}

ul {
  padding-left: 0px;
  margin-bottom: 0px;
}

li {
  list-style-type: none;
}

p {
  /* background-color: #d21825; */
  font-size: 14px;
  color: #999;
  line-height: 1.7;
  font-weight: 500;
  margin-bottom: 0px;
}


::-moz-selection {
  background: #222;
  color: #fff;
  text-shadow: none;
}

::selection {
  background: #222;
  color: #fff;
  text-shadow: none;
}

/*-----------------------------------------
	2. Align Holders
-------------------------------------------*/
.left-holder {
  text-align: left !important;
}

.right-holder {
  text-align: right !important;
}

.center-holder {
  text-align: center !important;
}

@media screen and (max-width:767px) {
  .text-center-xs {
    text-align: center !important;
  }

  .center-holder-xs {
    text-align: center !important;
  }
}

@media screen and (max-width:991px) {
  .text-center-sm {
    text-align: center !important;
  }
}

.vertical-middle {
  vertical-align: middle;
}

.vertical-center {
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

@media (max-width: 991px) {
  .vertical-center {
    display: block;
  }
}


/*-----------------------------------------
  3. Modals
-------------------------------------------*/
.modal {
  text-align: center;
  padding: 0 !important;
  border-radius: 10px;
}

.modal:before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
  margin-right: -4px;
  border-radius: 10px;
  z-index: 1;
}

.modal-dialog {
  width: 100%;
  display: inline-block;
  text-align: left;
  vertical-align: middle;
  z-index: 1;
}

@media screen and (max-width:767px) {
  .modal:before {
    content: '';
    display: inline-block;
    height: 0%;
    width: 100%;
    vertical-align: middle;
    margin-right: -4px;
    border-radius: 10px;
    z-index: 1;
  }
}

.modal-backdrop.show {
  opacity: .8;
  z-index: 2;
}

