/*-----------------------------------------
53. Color Switcher
-------------------------------------------*/
#style-switcher {
  left: -180px;
  position: fixed;
  top: 250px;
  width: 180px;
  z-index: 9999;
  background: #fff;
  border-radius: 0 0 3px;
  box-shadow: 0px 5px 15px 0 rgba(0, 0, 0, 0.1);
  -webkit-transition-duration: .7s;
  transition-duration: .7s;
}

#style-switcher div {
  padding: 5px 10px;
}

#style-switcher h2 {
  color: #222;
  font-size: 15px;
  font-weight: 600;
  margin-bottom: 0;
  margin-right: 0;
  margin-top: 0;
  padding: 15px 0px 7px 0px;
  text-align: center;
}

#style-switcher.opened {
  left: 0px;
}

#style-switcher h2 a {
  background: #222;
  border-radius: 0px 3px 3px 0px;
  display: block;
  height: 40px;
  position: absolute;
  right: -40px;
  top: 0;
  width: 40px;
}

#style-switcher h2 a i {
  color: #fff;
  font-size: 17px;
  left: 11px;
  position: absolute;
  top: 11px;
}

.colors {
  padding-left: 7px;
}

.colors {
  list-style: none;
  margin: 0px 0px 10px 0px;
  overflow: hidden;
}

.colors li {
  float: left;
}

.colors li a {
  cursor: pointer;
  display: block;
  height: 30px;
  margin: 7px 9px;
  width: 30px;
  border-radius: 30px;
}

