/*------------------------------------------*/
/*	     01 - General & Basic Styles
/*------------------------------------------*/
body {
	font-family: 'Open Sans', sans-serif;
	font-size: 13px;
	line-height: 21px;
	font-weight: 300;
	color: #666;
	background: url(../../AssetsMain/Images/patterns/1.png) fixed repeat;
}

strong,
b {
