/*First Icon Effect */
.icon-effect-1 {
	background-color: #fff;
	-webkit-transition: color 0.4s;
	-moz-transition: color 0.4s;
	transition: color 0.4s;
}

.gray-icon.icon-effect-1 {
	box-shadow: 0 0 0 3px #444;
	-o-box-shadow: 0 0 0 3px #444;
	-moz-box-shadow: 0 0 0 3px #444;
	-webkit-box-shadow: 0 0 0 3px #444;
}

.icon-effect-1:after {
	top: -2px;
	left: -2px;
	padding: 2px;
	z-index: -1;
	-webkit-transition: -webkit-transform 0.4s, opacity 0.4s;
	-moz-transition: -moz-transform 0.4s, opacity 0.4s;
	transition: transform 0.4s, opacity 0.4s;
}

.gray-icon.icon-effect-1:after {
	background: #444;
}

.icon-effect-1.gray-icon:hover,
.service-box:hover .gray-icon.icon-effect-1 {
	color: #444;
}

.icon-effect-1:hover:after,
.service-box:hover .icon-effect-1:after {
	-webkit-transform: scale(1.3);
	-moz-transform: scale(1.3);
	-ms-transform: scale(1.3);
	transform: scale(1.3);
	opacity: 0;
}

/*Seconed Icon Effect */
.icon-effect-2 {
	background: #fff;
	-webkit-transition: color 0.4s;
	-moz-transition: color 0.4s;
	transition: color 0.4s;
}

.gray-icon.icon-effect-2 {
	color: #444;
	box-shadow: 0 0 0 3px #444;
	-o-box-shadow: 0 0 0 3px #444;
	-moz-box-shadow: 0 0 0 3px #444;
	-webkit-box-shadow: 0 0 0 3px #444;
}

.icon-effect-2:after {
	top: -2px;
	left: -2px;
	padding: 2px;
	z-index: -1;
	-webkit-transform: scale(1.3);
	-moz-transform: scale(1.3);
	-ms-transform: scale(1.3);
	transform: scale(1.3);
	opacity: 0;
	-webkit-transition: -webkit-transform 0.4s, opacity 0.4s;
	-moz-transition: -moz-transform 0.4s, opacity 0.4s;
	transition: transform 0.4s, opacity 0.4s;
}

.gray-icon.icon-effect-2:after {
	background: #444;
}

.icon-effect-2:hover,
.service-box:hover .icon-effect-2,
.icon-effect-2.gray-icon:hover,
.service-box:hover .gray-icon.icon-effect-2 {
	color: #fff;
}

.icon-effect-2:hover:after,
.service-box:hover .icon-effect-2:after {
	-webkit-transform: scale(1);
	-moz-transform: scale(1);
	-ms-transform: scale(1);
	transform: scale(1);
	opacity: 1;
}

/*Third Icon Effect */
.icon-effect-3 {
	background: #fff;
}

.gray-icon.icon-effect-3 {
	color: #444;
