/* progress bar */

.skill p {
	margin-bottom: 7px;

}

.progress {
	overflow: visible;
	height: 18px;
	margin-bottom: 10px;
	background-color: #f9f9f9;
	border-radius: 0px;
	-webkit-box-shadow: none;
	box-shadow: none;
}

.progress-bar {
	float: left;
	height: 100%;
	font-size: 12px;
	color: #ffffff;
	text-align: center;
	-webkit-box-shadow: none;
	box-shadow: none;
	-webkit-transition: width 0.6s ease;
	transition: width 0.6s ease;
	position: relative;
}

.progress-bar-span {
	opacity: 1;
	position: absolute;
	top: -5px;
	background: #ACB2B8;
	padding: 3px 10px;
	color: #FFF;
	border-radius: 0px;
	right: 0px;
	-webkit-transition: all .5s ease-in-out;
	-moz-transition: all .5s ease-in-out;
	-o-transition: all .5s ease-in-out;
	-ms-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
}

.skill:hover .progress-bar-span {
	opacity: 1;
	-webkit-transition: all .5s ease-in-out;
	-moz-transition: all .5s ease-in-out;
	-o-transition: all .5s ease-in-out;
	-ms-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
}









